    .activateDisc-play{
        width: 243px;
        background-size: cover;
        border-radius: 50%;
        height: 246px;
        position: absolute;
        -webkit-animation: disc-spin infinite 5s linear;
                animation: disc-spin infinite 5s linear;
    }

    .activateDisc-pause{
        width: 243px;
        background-size: cover;
        border-radius: 50%;
        height: 246px;
        position: absolute;
        -webkit-animation: disc-spin infinite 5s linear;
                animation: disc-spin infinite 5s linear;
        -webkit-animation-play-state: paused;
                animation-play-state: paused;
    }
    
    @-webkit-keyframes disc-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }
    
    @keyframes disc-spin {
        from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
        to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }

