    /* Component ArtistBio */
    /* Style por l'effet diapo */
    @media screen and (max-width: 829px){
        .effectDiapo{
            margin-top: 35px !important;
        }
    }
    .effectDiapo{
        margin: 55px 0;
    }
    .effectDiapo span {
        font-family: "journal";
        z-index: 1;
        background: white;
        float: left;
        margin: 0 15px 30px;
        padding: 10px 10px 10px;
        text-align: center;
        text-decoration: none;
        box-shadow: 0 4px 6px rgba(0,0,0,.3);
        -webkit-box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
        -moz-box-shadow: 0 4px 6px rgba(0,0,0,.3);
        transition: all .15s linear;
        -webkit-transition: all .15s linear;
        -moz-transition: all .15s linear;
        transform: rotate(-10deg);
        -webkit-transform: rotate(-10deg);
        -moz-transform: rotate(-10deg);
    }
    .effectDiapo span:after {
        color: #fcec09;
        font-size: 40px;
        text-shadow: 5px 5px black;
        content: attr(title);
        font-family: 'stamp';
        position: absolute;
        margin-top: 0px;
        line-height: 1.1;
        transform: rotate(-15deg);
        -webkit-transform: rotate(-15deg);
        -moz-transform: rotate(-15deg);
    }
    .effectDiapo img {
        display: block;
        width: inherit;
        width: 225px;
        height: auto;
    }
    /* Fin effet diapo */


    /* Component ArtistConcerts */
    .artistConcert{margin-bottom: 75px;}
    /* Effet Post-It */
    @media screen and (max-width: 826px) {
        .displayPostIt{
            top:10px;
            transform: rotate(5deg);
            -moz-transform: rotate(5deg);
            -webkit-transform: rotate(5deg);
            -o-transform: rotate(5deg);
            -ms-transform: rotate(5deg);
        }
        .displayPostIt:nth-child(2){
            top:25px;
            transform: rotate(-4deg);
            -moz-transform: rotate(-4deg);
            -webkit-transform: rotate(-4deg);
            -o-transform: rotate(-4deg);
            -ms-transform: rotate(-4deg);
        }
        .displayPostIt:nth-child(3){
            top:48px;
            transform: rotate(10deg);
            -moz-transform: rotate(10deg);
            -webkit-transform: rotate(10deg);
            -o-transform: rotate(10deg);
            -ms-transform: rotate(10deg);
        }
    }
    @media screen and (min-width: 826px) {
        .displayPostIt{
            transform: rotate(5deg);
            -moz-transform: rotate(5deg);
            -webkit-transform: rotate(5deg);
            -o-transform: rotate(5deg);
            -ms-transform: rotate(5deg);
        }
        .displayPostIt:nth-child(2){
            top:10px;
            transform: rotate(-20deg);
            -moz-transform: rotate(-20deg);
            -webkit-transform: rotate(-20deg);
            -o-transform: rotate(-20deg);
            -ms-transform: rotate(-20deg);
        }
        .displayPostIt:nth-child(3){
            top:2px;
            transform: rotate(10deg);
            -moz-transform: rotate(10deg);
            -webkit-transform: rotate(10deg);
            -o-transform: rotate(10deg);
            -ms-transform: rotate(10deg);
        }
    }
    .displayPostIt{
        padding: 2%;
        width: 275px;    
        background-color: #FFFF77;
        box-shadow: 2px 4px 6px #444;
        -moz-box-shadow: 2px 4px 6px #444;
        -webkit-box-shadow: 2px 4px 6px #444;
        border-bottom-right-radius: 60px 10px;
        align-self: self-start;
        list-style: none;
    }
    .displayPostIt:nth-child(2){
        position:relative;
        background:#cfc;
    }
    .displayPostIt:nth-child(3){
        position:relative;
        background:#DEFF7C;
    }
    .displayPostIt li {
        font-family: "Open Sans";
        font-size: 17px;
    }

    .displayPostIt li:first-child{font-weight: bold;}
    .displayPostIt li:last-child{text-align: right;font-weight: bold;}
    .displayPostIt li:nth-child(2){font-style: italic;}
    .displayPostIt li:nth-child(4){font-weight:bold;}
    .displayPostIt li a{
        color: black;
        font-style: italic;
        font-size: 11px;
        text-decoration: underline;
    }
    /* Fin Effet Post-It */

