    /* Player Tracks */



    .info-and-control{
        z-index: 1;
    }
    .activateDisc-play{
        width: 243px;
        background-size: cover;
        border-radius: 50%;
        height: 246px;
        position: absolute;
        animation: disc-spin infinite 5s linear;
    }

    .activateDisc-pause{
        width: 243px;
        background-size: cover;
        border-radius: 50%;
        height: 246px;
        position: absolute;
        animation: disc-spin infinite 5s linear;
        animation-play-state: paused;
    }
    
    @keyframes disc-spin {
        from { transform: rotate(0deg); }
        to { transform: rotate(360deg); }
    }

